import React from "react";
import { Stack } from "@mui/material";

import CallItem from "../components/CallItem";

const ActiveList = ({ calls }) => {
  return (
    <>
      {calls.map((call) => (
        <Stack
          key={call.id}
          mb={2}
          py={1}
          px={2}
          border={"1px solid #dddddd"}
          borderRadius={1}
          sx={{ cursor: "pointer" }}
        >
          <CallItem call={call} />
        </Stack>
      ))}
    </>
  );
};

export default ActiveList;
