import React, { useState, useEffect, useContext, useMemo } from "react";
import Box from "@mui/material/Box";
import Tab from "@mui/material/Tab";
import TabContext from "@mui/lab/TabContext";
import TabList from "@mui/lab/TabList";
import TabPanel from "@mui/lab/TabPanel";

import { Context as FeedContext } from "../context/FeedContext";
import Header from "../components/Header";
import ArchiveList from "./ArchiveList";
import ActiveList from "./ActiveList";
import Loading from "../components/Loading";

const FeedList = () => {
  const [selectedTab, setSelectedTab] = useState("1");
  const { getAllCallList, state } = useContext(FeedContext);

  useEffect(() => {
    getAllCallList();
  }, []);

  const activeList = useMemo(() => {
    return state.calls.filter((v) => !v.is_archived);
  }, [state]);

  const archiveList = useMemo(() => {
    return state.calls.filter((v) => v.is_archived);
  }, [state]);

  return (
    <div className="container">
      <Header visible={false} />
      <Box
        sx={{ width: "100%", height: "calc(100% - 80px)", typography: "body1" }}
      >
        {state.loading && <Loading />}
        <TabContext value={selectedTab}>
          <Box sx={{ borderBottom: 1, borderColor: "divider" }}>
            <TabList
              onChange={(ev, val) => setSelectedTab(val)}
              aria-label="lab API tabs example"
              variant="fullWidth"
            >
              <Tab label="Active" value="1" />
              <Tab label="Archive" value="2" />
            </TabList>
          </Box>
          <Box
            sx={{
              height: "calc(100% - 50px)",
              overflowY: "auto",
              overflowX: "hidden",
            }}
          >
            <TabPanel value="1">
              <ActiveList calls={activeList} />
            </TabPanel>
            <TabPanel value="2">
              <ArchiveList calls={archiveList} />
            </TabPanel>
          </Box>
        </TabContext>
      </Box>
    </div>
  );
};

export default FeedList;
